:root{
  --Nav-font-size: 1.2em
}

*:focus{
  outline: 1px solid rgb(223, 52, 0);
}
*{
  color:white;
  margin: 0;
  padding: 0;

}
body{
  /* position: fixed;
  width: 100%; */
  background-color: #222222;
  -khtml-user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
#root{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
#content{
  width: 95vw;
  margin: auto;
  margin-top: 5vw;
}

#Header{
  background-color: #222222;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 2vw;
  font-size: 1.4vw;
  box-sizing: border-box;
  width:100%;
  min-height: 55px;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
}
#HeaderLeft{
  display: flex;
}
#HeaderRight{
  display: flex;
}
#NavHeader-ul{
  list-style: none;
  display: flex;
}
#NavHeader-ul li{margin-right: .8vw;}
#NavHeader-ul a{
  padding: .5vw 1vw;
  text-decoration: none;
  display: block;
}
#NavHeader-ul a:hover{
  background-color: #1a1a1a;
}
#Logo{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
}
#divImg{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
}
#NotFoundSearch{
  width: 100%;
  text-align: center;
  padding-top: 1vw;
}
#Search{
  background-color: #1a1a1a;
  border-radius: .5vw;
  position: relative;
  display: block;
  padding: .5vw 1vw;
  border: none;
  font-size: 1em;
  font-weight: lighter;
  font-family: 'Segoe UI',
    Tahoma,
    Geneva,
    Verdana,
    sans-serif;
  padding-left: 2.5vw;
  
}
#SearchMainContainer{
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  height: 100%;
  background: #000000d5;
}
#SearchContainer{
  padding-top: 1vw;
  width: 60%;
}
#SearchInputContainer{
  position: relative;
}
#SearchInputContainer >button{
  font-size: 2vw;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  background: #000000;
  display: flex;
  width: 3.5vw;
  border: none;
  min-width: 41px;
  align-items: center;
  justify-content: space-around;
}
#SearchInput{
  width: 100%;
  box-sizing: border-box;
  border: none;
  font-size: 1.3vw;
  padding: .8vw;
  background: #303030;
}
#SearchWhat{
  margin-top: .8vw;
}
#SearchWhat button{
  border: none;
  font-size: 1.1vw;
  padding: .5vw .8vw;
  background: #191919;
  margin-right: 1vw;
  cursor: pointer;
}
#SearchResult{
  margin-top: 1.5vw;
  width: 100%;
  height: 80%;
  overflow: auto;
  background: #151515;
}
#SearchResult a{
  display: flex;
  background: #151515;
  width: 100%;
  box-sizing: border-box;
  text-decoration: none;
  align-items: center;
  padding: 1vw;
}
#SearchResult a:hover{
  background: #0c0c0c;
}
#SearchResult a img{
  flex: 0 0 auto;
  display: block;
  background-color: #000000;
  width: 4.6vw;
  height: 6vw;
  min-width: 58px;
  min-height: 79px;
  margin-right: 1vw;
  box-sizing: border-box;
}
#SearchResult a div{
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 6vw;
  min-height: 79px;
  font-family: 'Segoe UI',
    Tahoma,
    Geneva,
    Verdana,
    sans-serif;
  font-weight: 100;
  
}
#SearchResult .SearchResultUser img {
  width: 5vw;
  height: 5vw;
  min-width: 60px;
  min-height: 60px;
  border-radius: 100%;
}

#SearchResult a div {
  justify-content: center;
}
#SearchResult a div h4{
  font-family: 'Segoe UI',
      Tahoma,
      Geneva,
      Verdana,
      sans-serif;
  font-weight: 100;
  font-size: 1.2vw;
}
#SearchResult a div p{
  color: #e97100;
  font-size: 1.2vw;
}
#Search::after{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: .5vw;
  content: " ";
  display: block;
  background: url("./Orion_search-magnify.svg");
  width: 2.1vw;
  height: 2.1vw;
  min-width: 30px;
  min-height: 30px;
  background-size: 100%;
}
#divAvatar{
  position: relative;
}
#Avatar{
  background: none;
  border: none;
}
#Avatar img{
  border-radius: 100%;
  min-height: 35px;
  min-width: 35px;
  width: 2.65vw;
  height: 2.65vw;
}

#AvatarBar{
  z-index: 99;
  font-size: .9em;
  position: absolute;
  top: 120%;
  right: 0;
  background: #1a1a1a;
  padding: 10px;
}
#AvatarBar a, #Login {
  border-radius: 5px;
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#AvatarBar a:hover{background-color: #141414;}
#Login{ background-color: #ce5200; }
#Login:hover{ background-color: #b64900; }


footer{
  position: absolute;
  left: 0;
  margin-top: 4.5vw;
  width: 100%;
  height: 9vw;
  min-height: 150px;
  background-color: #141414;
  z-index: -2;
}





#Error {
  width: 94vw;
  text-align: center;
  height: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#Error p{
  font-size: 1.3em;
}
#Error h1{
  font-size: 3em;
}





#MainSlider{
  overflow: hidden;
  margin: 1vw 0 1vw 0;
  width: 100%;
  user-select: none;
  min-height: 220px;
  height: 26vw;

}

#MainSliderContainer{
  min-height: 190px;
  height: 24.76vw;
  transition: .5s;
  position: relative;
  display: flex;
  width: 100%;
}
.MainSliderChild{
  box-sizing: border-box;
  background-color: #191919;
  min-width: 100%;
  padding: 1%;
}
.MainSliderChild a{
  outline: none;
  text-decoration: none;
  height: 100%;
  cursor: default;
  display: flex;
  align-items: center;
}

.MainSliderChild img{
  background-color: #181818;
  width: 19vw;
  height: 100%;
}
.MainSliderChild>a>div{
  overflow: hidden;
  pointer-events: none;
  min-height: 175px;
  height: 24.76vw;
  width: 100%;
  padding: 1vw;
  box-sizing: border-box;
}
#MainSlider{
  font-size: 2vw;
}
.MainSliderChild div p{
  font-size: 1.2vw;
}
.MainSliderChild div h2, .MainSliderChild div p{
  margin: 0px;
  padding: 0px;
}
.MainSliderChildInfo{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.MainSliderChildInfo> div{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 12vw;
  min-height: 80px;
}
.MainSliderChildInfo>div:first-child{
  height: 6vw;
  min-height: 40px;
}
.MainSliderChildInfo>div p{
  color: #9c9c9c;
}
.MainSliderChildInfo>div p:last-child{
  color: #c56900;
  display: flex;
  align-items: center;
}
.TitlesSliderGenres> div{
  display: flex;
}
.TitlesSliderGenres>div>div{
  width: 8vw;
  min-width: 75px;
}
.TitlesSliderGenres>div>div a{
  padding: .1vw;
  width: 7vw;
  min-width: 68px;
  font-size: 1vw;
  background: #202020;
  border-radius: 1vw;
  display: block;
  text-align: center;
}
#HotTitles{
  background-color: #181818;
  display: flex;
  width: 100%;
}
#HotTitlesHeader{
  background-color: #18181896;
  border-radius: 1vw 1vw 0 0 ;
