    margin: 0;
    height: 100%;
    font-size: 1em;
    box-sizing: border-box;
    text-align: center;
  }
  #mainButtons>button{
    height: 100%;
  }
  #mainButtons > div{
    width: 50%;
    height: auto;
    position: absolute;
    background: #141414;
    overflow: none;
    bottom: 0;
    right: 0;
    flex-direction: column;
    border-radius: 20px;
  }
  #mainButtons>div button{
    padding: 10px 16px;
  }
  #mainText{
    width: 80vw;
  }
  #settingsForText{
    top: auto;
    bottom: 0;
    width: 100%;
    height: 48px;
  }
  #settingsForText>div{
    display: flex;
    width: 60vw;
    justify-content: space-around;
  }
  .SettingsWindow{
    top: auto;
    left: 50%;
    bottom: 47px;
    width: 90%;
    height: 100%;
    transform: translateX(-50%);
    background: #191919;
    z-index: 1;
  }
  #SettingComments{
    display: flex;
    flex-direction: column;
    justify-content: end;
    height: 100%;
    padding-top: 110px;
    box-sizing: border-box;
  }
  #SettingComments >div:last-child{
    height: inherit;
  }
  #SettingChoose{
    height: 45%;
  }
  .MenuResultTitleContainer{
    width: 45%;
    height: 61vw;
    min-width: none;
    min-height: none;
  }
  #TitleHeaderBackgroundImg{
    display: block;
  }
  #LeftTitleInfo>img{
    height: 130px;
  }
  #LeftTitleInfoMiniInfo{
    height: 130px;
  }
  #RigthTitleInfoNameAndRateLeft p {
    font-size: 2em;
  }

  #LeftTitleInfoMiniInfo span {
    font-size: 1.2em;
